import React, { useEffect, useState } from 'react';
import defaultData from '../defaultData';

interface ContactProps extends React.HTMLProps<HTMLDivElement> {}

const Contact: React.FC<ContactProps> = () => {
  const clientnameId: string = 'contact__formClientname';
  const emailId: string = 'contact__formEmail';
  const email2Id: string = 'contact__formEmail2';
  const addressId: string = 'contact__formAddress';
  const inquiryId: string = 'contact__formInquiry';

  const [forms, setForms] = useState({
    [clientnameId]: '',
    [emailId]: '',
    [email2Id]: '',
    [addressId]: '',
    [inquiryId]: ''
  });

  const [isInvalids, setIsInvalids] = useState({
    [emailId]: false,
    [email2Id]: false
  });

  const [disabledButton, setDisabledButton] = useState(true);

  const checkForm = () => {
    let isInvalidEmailId: boolean = false;
    let isInvalidEmail2Id: boolean = false;

    if (
      forms[emailId] &&
      forms[email2Id] &&
      forms[emailId] !== forms[email2Id]
    ) {
      isInvalidEmailId = true;
      isInvalidEmail2Id = true;
    }

    // フォームボタンが押せなくなる条件を記述
    setDisabledButton(
      !forms[clientnameId] ||
        !forms[emailId] ||
        !forms[email2Id] ||
        !forms[inquiryId] ||
        isInvalidEmailId ||
        isInvalidEmail2Id
    );

    setIsInvalids({
      ...isInvalids,
      [emailId]: isInvalidEmailId,
      [email2Id]: isInvalidEmail2Id
    });
  };

  useEffect(() => {
    checkForm();
  }, [forms]);

  const onChangeForm = (key: string, value: string) => {
    setForms({
      ...forms,
      [key]: value
    });
  };

  return (
    <section id='contact'>
      <div className='container bg-gray p-5'>
        <h1>{defaultData.contact.title}</h1>
        <div className='contact-background'>
          <div className='row'>
            <div className='col-10 offset-1 col-lg-8 offset-lg-2'>
              <p className='text-center mb-5'>
                不動産の購入、売却、賃貸管理、その他お気軽にお問い合わせください。
              </p>
              <form
                action='https://docs.google.com/forms/u/2/d/e/1FAIpQLSezATzl5zb4rVgOMdghXC1LhyhzXYIVaytaFpF0Qh4d_fi1_Q/formResponse'
                method='POST'
              >
                {[
                  {
                    name: 'entry.1650788426',
                    id: clientnameId,
                    type: 'text',
                    required: true,
                    label: 'お名前',
                    example: '例：達磨 大志'
                  },
                  {
                    id: emailId,
                    type: 'email',
                    required: true,
                    label: 'メールアドレス',
                    example: '例：taishi@daruma-inv.com'
                  },
                  {
                    name: 'entry.1696363940',
                    id: email2Id,
                    type: 'email',
                    required: true,
                    label: 'メールアドレス',
                    description: '（確認用）',
                    example: '例：taishi@daruma-inv.com',
                    invalidMessage: 'メールアドレスが一致していません。',
                    invalidPaste: true
                  },
                  {
                    name: 'entry.1411341641',
                    id: addressId,
                    type: 'text',
                    label: 'ご住所',
                    example: '例：東京都渋谷区千駄ヶ谷2-10-5'
                  },
                  {
                    name: 'entry.881256958',
                    id: inquiryId,
                    required: true,
                    label: 'お問い合わせ内容',
                    example:
                      '※ 不動産の購入・売却、などなどお気軽にお問い合わせください。',
                    textarea: true,
                    rows: 3
                  }
                ].map((d: any, i: number) => {
                  const name: string = d.name || '';
                  const description: string = d.description || '';
                  const placeholder: string = d.placeholder || '';

                  return (
                    <div className='form-group row' key={i}>
                      <label htmlFor={d.id} className='col-md-4 col-form-label'>
                        <span
                          className={`badge rounded-0 ${
                            d.required ? 'badge-danger' : 'badge-secondary'
                          }`}
                        >
                          {d.required ? '必須' : '任意'}
                        </span>
                        <span className='ml-2'>{d.label}</span>
                        {description && (
                          <small className='text-muted'>{description}</small>
                        )}
                      </label>
                      <div className='col-md-8'>
                        {d.textarea ? (
                          <textarea
                            name={name}
                            className={`form-control form-control-lg ${
                              isInvalids[d.id] && 'is-invalid'
                            }`}
                            id={d.id}
                            rows={3}
                            required={!!d.required}
                            placeholder={placeholder}
                            value={forms[d.id]}
                            onChange={e => onChangeForm(d.id, e.target.value)}
                          ></textarea>
                        ) : (
                          <input
                            name={name}
                            className={`form-control form-control-lg ${
                              isInvalids[d.id] && 'is-invalid'
                            }`}
                            id={d.id}
                            type={d.type}
                            required={!!d.required}
                            placeholder={placeholder}
                            value={forms[d.id]}
                            onChange={e => onChangeForm(d.id, e.target.value)}
                            onPaste={e => {
                              if (d.invalidPaste) {
                                e.preventDefault();
                                return false;
                              }
                            }}
                          />
                        )}
                        {d.example && (
                          <small className='text-muted'>{d.example}</small>
                        )}
                        <div className='invalid-feedback'>
                          {d.invalidMessage}
                        </div>
                      </div>
                    </div>
                  );
                })}
                <div className='form-group row'>
                  <div className='col-sm-12'>
                    <button
                      type='submit'
                      className='btn btn-primary btn-block mt-4 py-3'
                      disabled={disabledButton}
                    >
                      上記の内容で送信する
                    </button>
                    <p className='mt-4 text-center'>
                      <small className='text-muted'>
                        ※上記フォームからの送信がうまくいかない場合は{' '}
                        <a
                          href='https://docs.google.com/forms/d/e/1FAIpQLSezATzl5zb4rVgOMdghXC1LhyhzXYIVaytaFpF0Qh4d_fi1_Q/viewform'
                          target='_blank'
                          className='text-muted'
                          style={{
                            textDecoration: 'underline'
                          }}
                        >
                          こちらをクリック
                        </a>{' '}
                        してください。
                      </small>
                    </p>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Contact;
